const SECURITY_HEADERS = {
  "Cross-Origin-Opener-Policy": "same-origin",
  "Cross-Origin-Embedder-Policy": "require-corp",
  "Cross-Origin-Resource-Policy": "same-origin",
  "Origin-Agent-Cluster": "?1"
};
const SECURITY_HEADER_ENTRIES = Object.entries(SECURITY_HEADERS);
const applySecurityHeaders = (headers) => {
  for (const [header, value] of SECURITY_HEADER_ENTRIES) {
    headers.set(header, value);
  }
};
const rewrapResponse = (response) => {
  const cloned = response.clone();
  const headers = new Headers(cloned.headers);
  applySecurityHeaders(headers);
  return new Response(cloned.body, {
    status: cloned.status,
    statusText: cloned.statusText,
    headers
  });
};
const handle = async ({ event, resolve }) => {
  const response = await resolve(event, {
    filterSerializedResponseHeaders: (name) => name === "content-type"
  });
  return rewrapResponse(response);
};
export {
  handle
};
